a{
  color: white
}
img{
  position:relative;
  top: 0;
}

.App {
  text-align: center;
  background-color: #000000;
  color: white;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.App-header {
  background-color: #000000;
  width:99vw;
  min-height: 10vh;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  font-size: calc(10px + 2vmin);
}

.Body{
width:80vw;
background-color: black;
display: flex;
flex-direction: column;
justify-content: center;
align-items: center;
}

.Menu{
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-start;
  padding-left: 5vw;
  padding-right: 5vw;
}

.Item{
  margin-right: 2vw;
}

.Intro{
  height:80vh;
  width:99vw;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}

.Personal{
  width:60%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;

}
.IntroImg{
  width:40%;
  padding-right: 10%;

}

.Footer{
  height:20vh;
  width:90%;
  background-color: #0d1115;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  padding-left:5vw;
}

.Icons{
  display:flex;
  width:10vw;
  flex-direction: row;
  justify-content: space-around;
  align-items: center;
}

.image{
  display: block;
  width: 100%;
  height: auto;
}
