a{
  color: white
}
img{
  position:relative;
  top: 0;
}

.Container{
  height: 80vh;
  width:90%;
  background-color:#111111;
  margin:5vw;
  border-radius: 50px;
  display:flex;
  justify-content: space-between;
  align-items: flex-start;
}

.ContainerInfo{
  height:100%;
  width:30%;
  padding-top: 10vh;
  padding-left: 0;
}

.ContainerImage{
  position: relative;
  height:100%;
  width:70%;
  display:flex;
  justify-content: space-between;
  align-items: center;
}
.ContainerImage:hover .overlay {
  height: 60%;
}

.overlay {
  position: absolute;
  bottom: 10vh;
  left: 5vw;
  right: 0;
  background-color: transparent;
  overflow: hidden;
  width: 70%;
  height: 0;
  transition: .5s ease;
}
.image{
  display: block;
  width: 100%;
  height: auto;
}
.imageOpacityOn{
  opacity:0.1;
  display:flex;
  justify-content: space-between;
  align-items: center;
}